import { IStyledCheckboxes } from "../../core/types/types";
import ExclamationMark from "../ExclamationMark/ExclamationMark";

const StyledCheckboxes: React.FC<IStyledCheckboxes> = ({
  serviceRequest,
  label,
  serviceList,
  onChange,
  checkboxErrorMessage,
}) => {
  return (
    <div className="flex flex-col items-start w-full mb-4 bg-white rounded px-4 py-3 border">
      <label htmlFor={label} className="text-base text-gray-500 pb-4">
        {label}
      </label>
      <div className="grid grid-cols-2 gap-1 w-full">
        {Object.values(serviceList).map((item: string, index: number) => {
          return (
            <div className="flex flex-row items-center w-full" key={index}>
              <input
                id={item}
                type="checkbox"
                name={item}
                value={item}
                title={label}
                checked={serviceRequest.service?.includes(item) ? true : false}
                className="mr-2"
                onChange={onChange}
              />
              <label htmlFor={item} className="text-base text-gray-800">
                {item}
              </label>
            </div>
          );
        })}
      </div>
      {checkboxErrorMessage && (
        <div className="mt-4 p-2 w-full border border-red-400 rounded flex items-center">
          <p className="text-base text-red-400 flex-grow mr-4">{checkboxErrorMessage}</p>
          <div className="mr-2 flex-shrink-0">
            <ExclamationMark />
          </div>
        </div>
      )}
    </div>
  );
};

export default StyledCheckboxes;
