import StandardServicePath from "../paths/StandardServicePath/StandardServicePath";
import StyledSelect from "../components/StyledInputs/StyledSelect";
import { serviceRequestList } from "../core/constants/constants";
import { IHome, IServiceRequestWithServiceString } from "../core/types/types";
import { apiService } from "../core/services/apiService";
import { AnimatePresence } from "framer-motion";
import { useHistory } from "react-router-dom";
import { FormEvent, useState } from "react";
import { AxiosResponse } from "axios";

const Home: React.FC<IHome> = ({
  initialState,
  serviceRequest,
  updateServiceRequest,
  updateHasCompletedForm,
}) => {
  const [formErrorMessage, updateFormErrorMessage] = useState<string | null>(null);
  const [checkboxErrorMessage, updateCheckboxErrorMessage] = useState<string | null>(null);
  const [isLoading, updateIsLoading] = useState(false);
  const history = useHistory();
  const errors = {
    checkboxError: "You need to select a service from the checkboxes above.",
    addressValidationError:
      "We were unable to find an address with those details. Please try again later or contact us.",
    apiError: "Something went wrong with this request. Please try again later or contact us.",
  };

  // SUBMIT METHOD FOR SERVICE REQUESTS WITH ONE OR MORE SERVICES (IE THEY HAVE CHECKBOXES)
  async function onSubmit(e: FormEvent) {
    e.preventDefault();
    updateFormErrorMessage(null);
    updateIsLoading(true);
    // ensure at least one checkbox is ticked
    if (!validateCheckboxes()) {
      updateCheckboxErrorMessage(errors.checkboxError);
      updateIsLoading(false);
      return;
    }

    if (await validateResidentOrRatepayer()) {
      const serviceRequestWithServiceString: IServiceRequestWithServiceString = Object.assign(
        {},
        serviceRequest,
        { service: serviceRequest.service?.toString() }
      );
      const response = await apiService.createServiceRequest(serviceRequestWithServiceString);
      handleResponse(response);
    }
  }

  // RESPONSE HANDLER FOR BOTH SUBMIT METHODS
  function handleResponse(response: AxiosResponse) {
    if (response?.status === 200) {
      if (parseInt(response.data)) {
        serviceRequest.ticketNumber = parseInt(response.data);
        updateHasCompletedForm(true);
        updateIsLoading(false);
        history.push("/confirmation");
      }
    } else {
      updateIsLoading(false);
      updateFormErrorMessage(errors.apiError);
    }
  }

  // VALIDATE EITHER AN ADDRESS OR AN ADDRESS AND ASSESSMENT NUMBER WITH TCC KERBSIDE DATA
  async function validateResidentOrRatepayer(): Promise<boolean> {
    const valuationNumber = serviceRequest.valuationNumber ?? "";

    const response = await apiService.checkAddress(
      serviceRequest.addressComponents,
      valuationNumber
    );

    if (response?.status === 200) {
      return true;
    } else if (response?.status === 400 && response?.data === "Failed") {
      updateFormErrorMessage(errors.addressValidationError);
      updateIsLoading(false);
      return false;
    }
    updateFormErrorMessage(errors.apiError);
    updateIsLoading(false);
    return false;
  }

  // VALIDATE CHECKBOXES: SERVICE ARRAY CAN BE BOTH EMPTY AND NULL TO FAIL
  function validateCheckboxes(): boolean {
    if (serviceRequest.service === null || !serviceRequest.service.length) {
      return false;
    }
    return true;
  }

  return (
    <div className="sm:mt-10 mb-10 w-11/12 md:w-8/12 xl:6/12">
      <div className="py-12 my-auto mx-auto flex flex-col items-start justify-start xl:w-4/5 2xl:w-6/12">
        <h1 className="heading-font w-full text-left mb-6">
          Report an issue with <br />
          your waste services
        </h1>
        {/******** SERVICE REQUEST DROPDOWN ********/}
        <StyledSelect
          label="Please select your service request"
          dropdownItems={serviceRequestList}
          onChange={(e) => {
            updateIsLoading(false); // when path is changed, reset the isLoading state for button animations
            updateFormErrorMessage(null); // reset main form error message
            updateCheckboxErrorMessage(null); // reset the checkbox error
            updateServiceRequest({
              ...initialState,
              serviceRequest: (e.target as HTMLInputElement).value,
            });
          }}
        />
        {/******** PATH FOR DAMAGED BIN SERVICE REQUESTS ********/}
        <AnimatePresence exitBeforeEnter>
          {serviceRequest?.serviceRequest === serviceRequestList.damagedBin && (
            <StandardServicePath
              key={1}
              serviceRequest={serviceRequest}
              updateServiceRequest={updateServiceRequest}
              onSubmit={(e) => onSubmit(e)}
              checkboxErrorMessage={checkboxErrorMessage}
              updateCheckboxErrorMessage={updateCheckboxErrorMessage}
              isLoading={isLoading}
              formErrorMessage={formErrorMessage}
            />
          )}
          {/******** PATH FOR MISSING BIN SERVICE REQUESTS ********/}
          {serviceRequest?.serviceRequest === serviceRequestList.missingBin && (
            <StandardServicePath
              key={2}
              serviceRequest={serviceRequest}
              updateServiceRequest={updateServiceRequest}
              onSubmit={(e) => onSubmit(e)}
              checkboxErrorMessage={checkboxErrorMessage}
              updateCheckboxErrorMessage={updateCheckboxErrorMessage}
              isLoading={isLoading}
              formErrorMessage={formErrorMessage}
            />
          )}
          {/******** PATH FOR ADDITIONAL BIN SERVICE REQUESTS ********/}
          {serviceRequest?.serviceRequest === serviceRequestList.additionalBin && (
            <StandardServicePath
              key={3}
              serviceRequest={serviceRequest}
              updateServiceRequest={updateServiceRequest}
              onSubmit={(e) => onSubmit(e)}
              checkboxErrorMessage={checkboxErrorMessage}
              updateCheckboxErrorMessage={updateCheckboxErrorMessage}
              isLoading={isLoading}
              formErrorMessage={formErrorMessage}
            />
          )}
          {/******** PATH FOR MISSED COLLECTION SERVICE REQUESTS ********/}
          {serviceRequest?.serviceRequest === serviceRequestList.missedCollection && (
            <StandardServicePath
              key={4}
              serviceRequest={serviceRequest}
              updateServiceRequest={updateServiceRequest}
              onSubmit={(e) => onSubmit(e)}
              checkboxErrorMessage={checkboxErrorMessage}
              updateCheckboxErrorMessage={updateCheckboxErrorMessage}
              isLoading={isLoading}
              formErrorMessage={formErrorMessage}
            />
          )}
        </AnimatePresence>
      </div>
    </div>
  );
};

export default Home;
